import React, { useState, useEffect } from "react";
import "./App.css";
import Button from "@material-ui/core/Button";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";
import TextField from "@material-ui/core/TextField";
import Todo from "./todo";
import { List } from "@material-ui/core";
import "./todo.css";

import db from "./firebase";

function App() {
  const [todo, setTodo] = useState([]);

  const [input, setInput] = useState("");

  const changeValue = function (e) {
    setInput(e.target.value);
  };

  const addTodoList = function (e) {
    e.preventDefault();
    db.collection("todos").add({
      todo: input,
    });
    setInput("");
  };

  useEffect(()=> (
    db.collection("todos").onSnapshot((snapshot) => {
      setTodo(snapshot.docs.map(todo => ({id: todo.id, todo: todo.data().todo})))
    })
  ), [])
  return (
    <div className="App">
      <h1>Hello world !</h1>
      <form>
        <FormControl>
          <FormLabel>Write your message</FormLabel>
          <TextField value={input} onChange={(event) => changeValue(event)} />
          <FormHelperText>hitting the button</FormHelperText>
          <Button
            disabled={!input}
            type="submit"
            onClick={(event) => addTodoList(event)}
            variant="contained"
            color="primary"
          >
            add Todo
          </Button>
        </FormControl>
      </form>

      <List className="todo__list">
        {todo && todo.map((item) => <Todo todo={item} key={item.id} />)}
      </List>
    </div>
  );
}

export default App;
